<template>
  <div class="initArea">
    <header>無限課金体験サイト</header>
    <div class="billBtn" @click="showBilling()">無限課金を体験する</div>
    <div class="infinityBilling" :class="isShow ? 'show' : ''">
      <div>
        <p class="bold">App Store</p>
        <p class="cancelBtn ml20" @click="close()">キャンセル</p>
      </div>
      <hr />
      <div>
        <img src="@/assets/appIcon.png" />
        <div class="ml20">
          <p>体験料</p>
          <p>無限課金体験</p>
          <p>APP内課金</p>
        </div>
      </div>
      <hr />
      <div>
        <p>アカウント</p>
        <p class="ml20">example-mail.com</p>
      </div>
      <hr />
      <div>
        <p>価格</p>
        <p class="ml20 bold">￥10,000</p>
      </div>
      <hr />
      <div class="column" :class="isBilling ? 'none' : ''">
        <img src="@/assets/fingerprint.jpg" alt="" />
        <p>Touch IDで支払う</p>
      </div>
      <div class="column" :class="isBilling ? '' : 'none'">
        <img src="@/assets/checkIcon.png" alt="" />
        <p class="">完了</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "InfinityBilling",
  props: {
    // msg: String
  },
  data() {
    return {
      isBilling: false,
      isShow: false,
      audio: new Audio(require("@/assets/billSound.mp3")),
    };
  },
  methods: {
    async showBilling() {
      this.isShow = true;
      await this.billing();
    },
    async billing() {
      setTimeout(() => {
        this.audio.play();
        this.isBilling = true;
      }, 2000);
      setTimeout(() => {
        this.close();
      }, 4000);
    },
    async close() {
      this.isShow = false;
      this.isBilling = false;
    },
  },
  mounted() {
    this.audio.load();
  },
};
</script>

